<template>
  <div class="platform-detail-page py-5">
    <div class="container" v-if="drone">
      <!-- 返回按钮 -->
      <div class="mb-4">
        <router-link :to="'/products/platform/' + drone.id.split('-')[0]" class="btn btn-outline-secondary">
          <i class="bi bi-arrow-left me-2"></i>返回产品列表
        </router-link>
      </div>

      <!-- 产品标题 -->
      <div class="row mb-4">
        <div class="col-12">
          <div class="product-badge mb-2" :class="getBadgeClass(drone.type)">
            {{ getTypeName(drone.type) }}
          </div>
          <h1 class="mb-3">{{ drone.name }}</h1>
          <p class="lead text-secondary">{{ drone.description }}</p>
        </div>
      </div>

      <!-- 产品导航标签 -->
      <ul class="nav nav-tabs mb-4" id="productTab" role="tablist">
        <li class="nav-item" role="presentation">
          <button class="nav-link active" id="overview-tab" data-bs-toggle="tab" data-bs-target="#overview" type="button" role="tab">产品概述</button>
        </li>
        <li class="nav-item" role="presentation">
          <button class="nav-link" id="specs-tab" data-bs-toggle="tab" data-bs-target="#specs" type="button" role="tab">技术参数</button>
        </li>
        <li class="nav-item" role="presentation">
          <button class="nav-link" id="features-tab" data-bs-toggle="tab" data-bs-target="#features" type="button" role="tab">特点优势</button>
        </li>
        <li class="nav-item" role="presentation">
          <button class="nav-link" id="applications-tab" data-bs-toggle="tab" data-bs-target="#applications" type="button" role="tab">应用场景</button>
        </li>
        <li class="nav-item" role="presentation" v-if="drone.video">
          <button class="nav-link" id="video-tab" data-bs-toggle="tab" data-bs-target="#video" type="button" role="tab">产品视频</button>
        </li>
      </ul>

      <!-- 标签内容 -->
      <div class="tab-content bg-white p-4 rounded shadow-sm" id="productTabContent">
        <!-- 产品概述 -->
        <div class="tab-pane fade show active" id="overview" role="tabpanel">
          <div class="row">
            <div class="col-lg-6">
              <div class="product-gallery">
                <div class="main-image mb-3">
                  <img :src="drone.image" class="img-fluid rounded" :alt="drone.name">
                </div>
                <div class="row g-2">
                  <div class="col-3" v-for="(img, index) in drone.gallery" :key="index">
                    <img :src="img" class="img-fluid rounded" :alt="'产品图片 ' + (index + 1)">
                  </div>
                </div>
              </div>
            </div>
            <div class="col-lg-6">
              <h3 class="product-title mb-3">{{ drone.name }}</h3>
              <p class="product-description mb-4">{{ drone.description }}</p>
              
              <h5 class="mb-3">主要特点</h5>
              <div class="row g-3 mb-4">
                <div class="col-lg-6" v-for="(feature, index) in drone.features.slice(0, 4)" :key="index">
                  <div class="feature-item d-flex align-items-center">
                    <div class="feature-icon me-2 text-accent">
                      <i class="bi bi-check-circle-fill"></i>
                    </div>
                    <div class="feature-text">{{ feature }}</div>
                  </div>
                </div>
              </div>
              
              <h5 class="mb-3">关键参数</h5>
              <div class="row g-3">
                <div class="col-6" v-for="(key, index) in getMainSpecs(drone)" :key="index">
                  <div class="spec-box p-3 rounded bg-light">
                    <div class="spec-title text-secondary small">{{ key }}</div>
                    <div class="spec-value fw-bold">{{ drone.specifications[key] }}</div>
                  </div>
                </div>
              </div>
              
              <div class="mt-4 pt-3 d-flex gap-3">
                <button class="btn btn-primary">获取报价</button>
                <button class="btn btn-outline-secondary">下载产品手册</button>
              </div>
            </div>
          </div>
        </div>

        <!-- 技术参数 -->
        <div class="tab-pane fade" id="specs" role="tabpanel">
          <div class="row">
            <div class="col-lg-6">
              <div class="table-responsive">
                <table class="table table-hover">
                  <thead>
                    <tr>
                      <th scope="col" class="bg-light">参数</th>
                      <th scope="col" class="bg-light">规格</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(spec, key) in drone.specifications" :key="key">
                      <td>{{ key }}</td>
                      <td>{{ spec }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <div class="col-lg-6">
              <img :src="drone.image" class="img-fluid rounded" :alt="drone.name">
            </div>
          </div>
        </div>

        <!-- 特点优势 -->
        <div class="tab-pane fade" id="features" role="tabpanel">
          <div class="row">
            <div class="col-lg-6">
              <h4 class="mb-4">产品特点</h4>
              <ul class="feature-list">
                <li v-for="(feature, index) in drone.features" :key="index" class="mb-3">
                  <div class="d-flex">
                    <div class="feature-icon text-accent me-3">
                      <i class="bi bi-check-circle-fill fs-4"></i>
                    </div>
                    <div>
                      <p class="mb-0">{{ feature }}</p>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
            <div class="col-lg-6">
              <div class="feature-gallery">
                <img :src="drone.gallery[0]" class="img-fluid rounded mb-3" alt="产品特点">
                <div class="row g-3">
                  <div class="col-6" v-for="(img, index) in drone.gallery.slice(1)" :key="index">
                    <img :src="img" class="img-fluid rounded" :alt="'产品特点 ' + (index + 1)">
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- 应用场景 -->
        <div class="tab-pane fade" id="applications" role="tabpanel">
          <div class="row">
            <div class="col-12 mb-4">
              <h4>适用场景</h4>
              <p class="text-secondary">本产品适用于以下行业和应用场景</p>
            </div>
          </div>
          <div class="row g-4">
            <div class="col-lg-4 col-md-6" v-for="(app, index) in drone.applications" :key="index">
              <div class="application-card p-4 rounded shadow-sm h-100">
                <div class="application-icon mb-3 text-accent">
                  <i :class="getApplicationIcon(app)"></i>
                </div>
                <h5>{{ app }}</h5>
                <p class="text-secondary mb-0">{{ getApplicationDesc(app) }}</p>
              </div>
            </div>
          </div>
        </div>

        <!-- 产品视频 -->
        <div class="tab-pane fade" id="video" role="tabpanel" v-if="drone.video">
          <div class="row">
            <div class="col-12 mb-4">
              <h4>产品视频展示</h4>
              <p class="text-secondary">观看详细的产品演示和操作视频</p>
            </div>
            <div class="col-12">
              <div class="ratio ratio-16x9">
                <iframe :src="drone.video" title="产品视频" allowfullscreen></iframe>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <div class="container" v-else>
      <div class="alert alert-warning">
        产品信息加载中或未找到，请稍候...
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PlatformDetail',
  data() {
    return {
      drone: null,
      drones: []
    }
  },
  created() {
    const id = this.$route.params.id;
    // 优先从本地 this.drones 查找
    if (this.drones.length) {
      this.drone = this.drones.find(d => d.id === id);
    }
    // 如果本地没有，尝试从 localStorage 获取
    if (!this.drone) {
      try {
        const dronesStr = localStorage.getItem('allDrones');
        if (dronesStr) {
          const drones = JSON.parse(dronesStr);
          this.drone = drones.find(d => d.id === id);
        }
      } catch (e) {}
    }
    // 如果还没有，自动内置一份静态数据
    if (!this.drone) {
      const staticDrones = [
        {
          id: 'fpv-01',
          type: 'fpv',
          name: 'SpeedRacer Pro',
          description: '专业穿越竞速无人机，极致的飞行体验',
          image: '/images/home/product/product1.png',
          gallery: ['/images/home/product/product1.png'],
          features: ['高速穿越飞行体验', '低延迟FPV图传系统', '灵活操控与特技飞行', '高清航拍能力'],
          specifications: {
            '最大起飞重量': '0.8kg',
            '续航时间': '20分钟',
            '最大飞行速度': '180km/h',
            '最大抗风等级': '5级'
          },
          applications: ['FPV竞速', '特技飞行', '航拍摄影', '娱乐休闲'],
          video: ''
        },
        {
          id: 'mr-01',
          type: 'multiRotor',
          name: 'QuadX 800',
          description: '高精度多旋翼无人机，适合精细作业和复杂环境',
          image: '/images/home/product/product2.png',
          gallery: ['/images/home/product/product2.png'],
          features: ['精准悬停与定位', '高效作业能力', '多任务载荷', '智能飞控系统'],
          specifications: {
            '最大起飞重量': '8kg',
            '续航时间': '35分钟',
            '最大飞行高度': '4500m',
            '最大抗风等级': '7级'
          },
          applications: ['航拍', '测绘', '监控', '农业植保'],
          video: ''
        }
      ];
      this.drone = staticDrones.find(d => d.id === id);
    }
    if (!this.drone) {
      console.warn('未找到产品', id);
    }
  },
  methods: {
    getMainSpecs(drone) {
      const specKeys = {
        fixedWing: ['最大起飞重量', '最大载重', '续航时间', '最大飞行速度'],
        multiRotor: ['最大起飞重量', '最大载重', '续航时间', '最大飞行速度'],
        helicopter: ['最大起飞重量', '最大载重', '续航时间', '最大飞行速度'],
        fpv: ['机身尺寸', '重量(不含电池)', '最大速度', '续航时间'],
        system: ['最大集群数量', '控制距离', '编队精度', '响应时间']
      };
      const type = drone.type || 'multiRotor';
      return specKeys[type] || Object.keys(drone.specifications).slice(0, 4);
    },
    getBadgeClass(type) {
      const classes = {
        fixedWing: 'bg-primary',
        multiRotor: 'bg-success',
        helicopter: 'bg-danger',
        fpv: 'bg-warning',
        system: 'bg-info'
      };
      return classes[type] || 'bg-secondary';
    },
    getTypeName(type) {
      const names = {
        fixedWing: '固定翼',
        multiRotor: '多旋翼',
        helicopter: '直升机',
        fpv: 'FPV',
        system: '系统平台'
      };
      return names[type] || '未分类';
    },
    getApplicationIcon(app) {
      return 'bi bi-rocket';
    },
    getApplicationDesc(app) {
      return '';
    }
  }
}
</script>

<style scoped>
.platform-detail-page {
  min-height: 80vh;
  background-color: #f8f9fa;
}

.product-badge {
  display: inline-block;
  padding: 0.35em 0.65em;
  font-size: 0.75em;
  font-weight: 500;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.25rem;
}

.feature-item {
  padding: 0.5rem;
  background-color: rgba(0,0,0,0.02);
  border-radius: 0.25rem;
  height: 100%;
}

.text-accent {
  color: var(--accent-color);
}

.spec-box {
  border: 1px solid rgba(0,0,0,0.05);
}

.feature-list {
  list-style: none;
  padding-left: 0;
}

.application-card {
  background-color: white;
  transition: all 0.3s ease;
}

.application-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.08) !important;
}

.application-icon {
  font-size: 2rem;
}

.tab-content {
  border-radius: 0 0 0.25rem 0.25rem;
}

.nav-tabs .nav-link {
  color: var(--secondary-color);
  font-weight: 500;
}

.nav-tabs .nav-link.active {
  color: var(--primary-color);
  font-weight: 600;
}
</style> 